import {ajax} from '../../common/js/util.js'
import { Toast } from 'vant'
export default {
	data() {
		return {
			Color: '#DE2910',
			value: 1,
			backColor: '#FFFFFF',
			currentTime: new Date(),
			minDate: new Date(),
			show: false,
			time: "请选择到厅时间",
			name: "",
			user: "",
			tel: "",
			id: "",
			isVip: 0,
			notice: '',
			price: 0
		}
	},
	components: {
		Toast
	},
	onLoad(option) {
		//刷新保留用户uid
		if (this.getRequest('uid')) {
			var uids = this.getRequest('uid')
			this.$store.commit('changeUid', uids)
		}
		this.name = decodeURIComponent(option.name) 
		this.id = option.id
		this.isVip = option.isVip
		this.notice = decodeURIComponent(option.notice)
		this.price = option.price
	},
	methods: {
		getRequest(variable) {
			var query = window.location.search.substring(1);
			var vars = query.split("&");
			for (var i = 0; i < vars.length; i++) {
				var pair = vars[i].split("=");
				if (pair[0] == variable) {
					return pair[1];
				}
			}
			return (false);
		},
		init() {
			let self = this
			let datas = {
				vipHallId: this.id,
				uid: this.$store.state.uid
			}
			let data = {
				url: '/api/gzh/vipHallDetail',
				data: datas,
				success: function(res){
					console.log(res)
					if(res.data.result == 0){
						self.imgs = res.data.images
						self.listData = res.data
						self.serviceList = res.data.serviceList
						self.assistantList = res.data.assistantList
						self.name = res.data.title
						self.isVip = res.data.isVip
					}
				}
			}
			ajax(data)
		},
		confirm(val) {
			this.time = this.dateFtt("yyyy-MM-dd hh:mm", val)
			this.show = false
		},
		cancel() {
			this.show = false
		},
		choiceTime() {
			this.show = true
		},
		dateFtt(fmt, date) { //author: meizz   
			var o = {
				"M+": date.getMonth() + 1, //月份   
				"d+": date.getDate(), //日   
				"h+": date.getHours(), //小时   
				"m+": date.getMinutes(), //分   
				"s+": date.getSeconds(), //秒   
				"q+": Math.floor((date.getMonth() + 3) / 3), //季度   
				"S": date.getMilliseconds() //毫秒   
			}
			if (/(y+)/.test(fmt))
				fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length))
			for (var k in o)
				if (new RegExp("(" + k + ")").test(fmt))
					fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)))
			return fmt
		},
		paySuc() {
			if(this.time == '请选择到厅时间'){
				Toast('请选择时间')
				return
			}
			
			if(this.user == ''){
				Toast('请输入联系人')
				return
			}
			
			if (this.tel == '') {
				Toast('手机号不能为空')
				return
			} else if (!(/^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/.test(this.tel))) {
				Toast('手机号格式不正确')
				return
			}
			
			let self = this
			let datas = {
				uid: this.$store.state.uid,
				vipHallId: self.id,
				numbers: self.value,
				reserveDate: self.time,
				username: self.user,
				phone: self.tel,
				amount: self.price
			}
			let data = {
				url: '/api/gzh/bookingVipHall',
				data: datas,
				success: function(res){
					if(res.data.result == 0){
						uni.navigateTo({
							url: '../pay/pay?id=' + self.id + '&order=' + res.data.orderId
						})
					}else{
						Toast(res.data.resultNote)
					}
				}
			}
			ajax(data)
		},
		goOrder() {
			if(this.time == '请选择到厅时间'){
				Toast('请选择时间')
				return
			}
			
			if(this.user == ''){
				Toast('请输入联系人')
				return
			}
			
			if (this.tel == '') {
				Toast('手机号不能为空')
				return
			} else if (!(/^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/.test(this.tel))) {
				Toast('手机号格式不正确')
				return
			}
			
			let self = this
			let datas = {
				uid: this.$store.state.uid,
				vipHallId: self.id,
				numbers: self.value,
				reserveDate: self.time,
				username: self.user,
				phone: self.tel,
				amount: 0
			}
			let data = {
				url: '/api/gzh/bookingVipHall',
				data: datas,
				success: function(res){
					if(res.data.result == 0){
						uni.navigateTo({
							url: '../vipOrder/vipOrder'
						})
					}else{
						Toast(res.data.resultNote)
					}
				}
			}
			ajax(data)
		}
	}
}
